import Link from 'next/link';

const Header = () => (
	<div className="header">
		<Link href="/index">
			<a className="btn">Main</a>
		</Link>
		<Link href="/about">
			<a className="btn">About Page</a>
		</Link>
		<style jsx>{`
			.header a {
				font-family: Arial, sans-serif;
				padding: 7px;
				selection: none;
			}
		`}</style>
	</div>
);

export default Header;
